<template>
  <div class="mapbox-map">
    <div id="mapbox-map-container"></div>
    <slot></slot>
  </div>
</template>

<script>
import MixinMap from '../../mixins/Map.js';

var mapboxgl = require('mapbox-gl/dist/mapbox-gl.js');

export default {
  name: 'mapbox-map',
  mixins: [MixinMap],

  props: {
    accessToken: {
      type: String,
      required: false,
      default:
        'pk.eyJ1IjoicGhpZGlhcyIsImEiOiJjanhscmU3eHMwOGg0M3RudDFsN3U4NmxrIn0.fxY9zVDEfDHhpYnlyBuPUg',
    },
  },

  data() {
    return {
      map: null,
    };
  },

  provide() {
    return {
      getMap: () => this.map,
      mapboxgl,
    };
  },

  watch: {
    center(newValue) {
      // this.map.setCenter(newValue);
      this.map.panTo(newValue, { duration: 40 });
    },

    zoom() {
      this.map.setZoom(this.zoom);
    },
  },

  mounted() {
    this.initialize();
  },

  methods: {
    initialize() {
      mapboxgl.accessToken = this.accessToken;

      this.map = new mapboxgl.Map({
        container: 'mapbox-map-container',
        style: 'mapbox://styles/mapbox/streets-v11',
        // style: 'mapbox://styles/mapbox/light-v10',
        center: this.center,
        zoom: this.zoom,

        pitch: 45,
        bearing: -17.6,
      });

      this.addBuildingsLayer();
    },

    addBuildingsLayer() {
      if (!this.map) {
        return;
      }

      this.map.on('load', () => {
        // Insert the layer beneath any symbol layer.
        var layers = this.map.getStyle().layers;

        var labelLayerId;
        for (var i = 0; i < layers.length; i++) {
          if (layers[i].type === 'symbol' && layers[i].layout['text-field']) {
            labelLayerId = layers[i].id;
            break;
          }
        }

        this.map.addLayer(
          {
            id: '3d-buildings',
            source: 'composite',
            'source-layer': 'building',
            filter: ['==', 'extrude', 'true'],
            type: 'fill-extrusion',
            minzoom: 15,
            paint: {
              'fill-extrusion-color': '#aaa',

              // use an 'interpolate' expression to add a smooth transition effect to the
              // buildings as the user zooms in
              'fill-extrusion-height': [
                'interpolate',
                ['linear'],
                ['zoom'],
                15,
                0,
                15.05,
                ['get', 'height'],
              ],
              'fill-extrusion-base': [
                'interpolate',
                ['linear'],
                ['zoom'],
                15,
                0,
                15.05,
                ['get', 'min_height'],
              ],
              'fill-extrusion-opacity': 0.6,
            },
          },
          labelLayerId
        );
      });
    },
  },
};
</script>

<style lang="scss">
@import url('https://api.mapbox.com/mapbox-gl-js/v1.0.0/mapbox-gl.css');

#mapbox-map-container {
  min-height: 500px;
}
</style>